<template>
  <div class="upload">
    <div class="fitstDiv">
      <div class="examstatus">
        <p class="examTit">考核状态</p>
        <p class="examBtn" >
          <!--  @click="selectStyle (item, $index) " :class="{'active':item.active,'unactive':!item.active}" -->
          <span class="statusBtn" v-for="(item,$index) in items" :value="item.id"  @click="aaa($event)">{{item.name}}</span>
        </p>
      </div>
    </div>
    <!-- 上传视频 -->
    <div class="secDiv">
      <div class="uploadVideo">
        <p class="examTit">上传视频</p>
        <div class="up">
          <div class="upmv">
            <div class="" @click="upload()" v-show="btn">
              <img src="../../../static/img/jia.png" alt="" class="jia">
              <p class="upmsg">上传视频不能超过30M</p>
            </div>
            <div class="loding" v-show="load">
              <img src="../../../static/img/loading.gif" alt="" class="jia">
              <p class="upmsg2">上传中...</p>
            </div>
          </div>
          <input type="file" name="file" value="上传视频" @change="fileChange($event)" accept="video/*"  id="file" hidden="hidden">
            <span class="close" @click="closeVid($event)" v-show="mv"><img src="../../../static/img/close.png" alt=""></span>
          <div class="vidCon" v-show="mv" v-if="mv" v-for="value in vidArr" v-html="value">
            <!-- v-if="mv" v-for="value in vidArr" v-html="value" -->
            <!-- 这里加删除按钮 -->

          </div>
        </div>
      </div>
    </div>

    <!-- 教学评价 -->
    <div class="thirdDiv">
      <div class="takemsg">
        <p class="examTit">教学评价</p>
        <div class="setMsg">
          <textarea name="name" rows="" cols="" placeholder="说说您对该学员此次课程的评价" ref="common"></textarea>
        </div>
      </div>
    </div>
    <!-- 按钮 -->
    <div class="sureCon">
      <div class="sure" @click="sure()">
        完成
      </div>
    </div>

  </div>
</template>

<script>
import Vue from 'vue'
import AjaxConfig from "../../ajaxConfig/ajaxConfig.vue"
import {sessid} from '../../libs/sessid.js'
export default {
  data() {
    return{
      active: false,
      mv:false,
      btn:true,
      dan:"",
      load:false,
      Vid:'',
      vidArr:[],
      serviceType:"",
      aid:"",
      astatus:"",
      userId:"",
      sessionId:"",
      reserveId:"",
      reserveType:"",
      items:[
        {id:"0",name:"通过"},
        {id:"1",name:"未通过"},
      ]
    }
  },
  created:function () {
    document.title = "上传学习档案";

    var sessionId = localStorage.getItem("json");
    sessid();
    this.sessionId = sessionId;
    // 拿到sessionId
    var uploadInfo = localStorage.getItem("uploadInfo");
    uploadInfo = JSON.parse(uploadInfo);

    console.log(uploadInfo);
    this.aid = uploadInfo.aid;
    this.astatus = uploadInfo.astatus;
    this.serviceType = uploadInfo.serviceType;
    this.userId = uploadInfo.userId;
    this.reserveId = uploadInfo.reserveId;
    this.reserveType = uploadInfo.reserveType;


  },
  mounted:function () {
    const self = this;
    if(self.astatus == 0){
      $('.examBtn span:first-of-type').addClass("active");
      $('.examBtn span:last-of-type').removeClass("active");
    }else if (self.astatus == 1) {
      $('.examBtn span:first-of-type').removeClass("active");
      $('.examBtn span:last-of-type').addClass("active");
    }



  },
  methods:{
    selectStyle (item, index) {
　　　　　　this.$nextTick(function () {
　　　　　　　　this.items.forEach(function (item) {
　　　　　　　　　　Vue.set(item,'active',false);
　　　　　　　　});
　　　　　　　　Vue.set(item,'active',true);
　　　　　　});
　　　　},
    upload() {
      $('#file').click();
    },
    fileChange(e){
      var target = e.currentTarget
          var fileSize = 0;
          fileSize = target.files[0].size;
          var size = fileSize / 1024 / 1024;
          if (size > 30) {
            alert("文件不能大于30M");
          }else{
            var file = $('#file')[0].files[0];
                this.uploadEdieFile(file);
          }
      },
      uploadEdieFile:function (file){
        var _this = this;
          var formdata = new FormData();
          formdata.append('file',file);
           _this.btn = false;
          _this.load = true;
          $.ajax({
            url:'/api/common/upload/video',
            type:'POST',
            data:formdata,
            processData:false,
            contentType:false,
            success:function(json){
              console.log(json);
              if(json.code == 0){
                var vidUrl = json.data.url;

                  var time = setInterval(function () {
                    var newUrl = vidUrl+'?'+new Date().getTime();
                    $.ajax({
                     url: newUrl,
                     type: 'GET',
                     async:false,
                     complete: function(response){
                      if(response.status == 404){
                          $('.upmsg2').html('转码中...');
                        // 动画，解码中
                     }else if(response.status == 200){

                        window.clearInterval(time);
                        _this.Vid = newUrl;
                        // alert(_this.Vid);
                       // 加号消失，video出现
                       _this.load = false;
                       _this.mv = true;
                       // 第一帧动画
                      //  var video;

                        // var initialize = function() {
                        // video = document.getElementById("video");
                        // video.addEventListener('loadeddata',captureImage);
                        // };
                        var video = '<video src='+newUrl+' controls class="vid" id="video" poster="./static/img/play.png"></video>'
                        _this.vidArr.push(video);



                     }else{
                       alert("上传失败");
                       _this.btn = true;
                       _this.load = false;
                        _this.mv = false;
                     }
                     }
                    });
                  },1500);

                  var time2 = setInterval(function () {
                      var obj = document.querySelector('.vidCon');
                      var haveVid = obj.getElementsByTagName('video');
                    if(haveVid.length<0){

                    }else{
                      var videoImg =document.getElementById("video");
                      var scale = 0.8;
                      window.clearInterval(time2);
                      var canvas = document.createElement("canvas");
                      canvas.width = videoImg.videoWidth * scale;
                      canvas.height = videoImg.videoHeight * scale;
                      canvas.getContext('2d').drawImage(videoImg, 0, 0, canvas.width, canvas.height);

                      // var img = document.createElement("img");
                      var img = canvas.toDataURL("image/png");
                      console.log(img);
                      // output.appendChild(img);
                      // videoImg.setAttribute("poster",img);

                    }
                  },1500);


              }
            },
            error:function (error) {
                error = JSON.stringify(error);
                alert("上传失败");
                _this.btn = true;
                _this.load = false;
                 _this.mv = false;

            }
          });
        },
        //上传视频的方法 ↑
        closeVid(e){
          // 删除视频
          var closeBtn = e.currentTarget;
          $(closeBtn).fadeOut(100);
          $('.vidCon').find('video').remove();
          this.mv = false;
          this.load = false;
          this.btn = true;
          this.Vid = "";
        },
        sure(){
          var uploadStudy = AjaxConfig.api.uploadStudy;
          var msg = this.$refs.common.value;
          var status;

          $('.examBtn span').each(function (index,value) {
            if($(value).hasClass('active')){
               status =  $(this).attr('value')
            }
          });
          var aid = this.aid;
          if(status == "" || status == null || status == undefined){
            alert("请选择学员是否通过");
          }else if (this.Vid == null || this.Vid == "" || this.Vid == undefined) {
            alert("请先上传视频");
          }else{
            this.$http.post(uploadStudy,{
                "userId":this.userId,
                "videoUrl":this.Vid,
                "common":msg,
                "aid":aid,
                "astatus":status,
                "serviceType":this.serviceType,
                "reserveNumber":this.reserveId,
                "type":this.reserveType
            },{
               headers: {"X-SESSION-ID":this.sessionId},
               emulateJSON: false
            }).then(function (respone) {
              var res = respone.data;
              if(res.code == 0){
                // token 未失效，直接跳转页面
                alert("填写成功");
                this.$router.push("/study/studyProject");
              }else{
                alert("添加失败");
              }
            },function () {
                alert("请检查网络连接");
            });
          }


        },
        aaa(e) {
          $('.examBtn span').removeClass("active");
          var thisSpan = e.currentTarget;
          $(thisSpan).addClass("active");
        }


  }
}
</script>

<style>
.active{
  background-color: #4ab6b5 !important;
  color: #fff !important;
}

.fitstDiv{
  background-color: #fff;
  height: 5.5rem;
  width: 100%;
  margin-top: 0.5rem;
}
.examstatus{
  width: 92%;
  margin: auto;

  margin-bottom: 0.75rem;
}
.examTit{
  padding-top: 0.8rem;
  color: #000;
  font-size: 0.65rem;
  padding-bottom: 0.35rem;
}
.examBtn{
  height: 2rem;
  line-height: 2rem;
  margin-top: 0.4rem;
}
.examBtn .statusBtn{
  display: inline-block;
  width: 4.2rem;
  height: 1.75rem;
  text-align: center;
  line-height: 1.75rem;
  border-radius: 5px;
  background-color:#ededed ;
  color: #bcbcbc;
  font-size: 0.7rem;
  margin-right: 1rem;
}
.secDiv{
  margin-top: 0.5rem;
  height: 11.5rem;
  background-color: #fff;
}
.uploadVideo{
  width: 92%;
  margin: auto;
}
.up{
  width: 100%;
  height: 9.4rem;
  text-align: center;
  position: relative;
}
.upmv{
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  z-index: 2;
}
.jia{
  width: 3.1rem;
  height: 3.1rem;
  padding-bottom: 0.6rem;
  padding-top: 2rem;
}
.upmsg{
  font-size: 0.6rem;
  color: #c9c9c9;
  font-weight: lighter;
}
.upmsg2{
  font-size: 0.8rem;
  color: #c9c9c9;
}
.up .vidCon{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
  z-index: 100;
}
.up .close{
  position: absolute;
  right: 0rem;
  top: -1.5rem;
  display: inline-block;
  width: 1.5rem;
  height: 1.5rem;
  z-index: 1000;
}
.up .close img{
  width: 100%;
  height: 100%;
}
.up .vidCon video{
  width: 100%;
  height: 100%;
  object-fit:fill;
}

.thirdDiv{
  margin-top: 0.5rem;
  background-color: #fff;
  padding-bottom: 2rem;
}
.takemsg{
  width: 92%;
  margin: auto;
}
.setMsg{
  width: 95%;
  height: 5.5rem;
  background-color: #ededed;
  padding:0.2rem 0.5rem;
}
.setMsg textarea{
  background-color: #ededed;
  width: 100%;
  height: 100%;
  color: #c9c9c9;
  border: none;
  outline: none;
  font-size: 0.7rem;
  resize:none;
}
::-webkit-input-placeholder { /* WebKit browsers */
color: #c9c9c9;
}
.sure{
  /*position: fixed;
  bottom: 0px;
  left: 0px;
  right: 0px;*/
  width: 92%;
  margin: auto;
  height: 2.5rem;
  z-index: 10;
  background-color: #4ab6b5;
  color: #fff;
  text-align: center;
  line-height: 2.5rem;
  font-size: 0.7rem;
  border-bottom: 1rem solid #fff;
}
.sureCon{
  width: 100%;
  background-color: #fff;
}
</style>



